.bg_img {
    width: 100%;
    height: 140%;
    position: absolute;
    left: 0;
    top: 0;
}

.bg_img_div {
    z-index: 0;
}

.cover_img_div {
    position: absolute;
    z-index: 10;
    left: 50%;
    top: 150px;
    transform: translate(-50%, 0%);
    font-size: 50px;
}

.movie_info, .movie_info p {
    margin: 0;
}

.genres {
    margin: 10px;
    background-color: beige;
    color: firebrick;
    font-size: 30px;
    border-radius: 10px;
    padding: 5px;
}

.movie_title {
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.intro {
    margin-top: 30px !important;
    font-size: 20px;
    font-family: Verdana, Geneva, Arial, sans-serif;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.year {
    margin-top: 20px !important;
    font-size: 40px;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}