.movie_outline {
    font-family: "Comic Sans", "Apple Chancery", cursive;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.star {
    position: absolute;
    color: yellow;
    left: 15px;
    overflow: hidden;
}

.blankStar {
    position: absolute;
    color: lightgray;
    left: 15px;
}

img {
    vertical-align: top;
}
.banner_img, .banner_bg{
    display:inline-block;
    position: relative;
    margin: 20px;
}
.banner_img:hover:after,
.banner_img:hover > .hover_text,
.banner_bg:hover:after,
.banner_bg:hover > .hover_text
{
    display:block;
}
.banner_img:after,.banner_bg:after,.hover_text{
    display:none;
}
.banner_img:after,.banner_bg:after{
    content:'';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 10;
    transform: scale(1.2);
}
.banner_img {
    /* overflow: hidden; */
}
.banner_img img{
    height: 340px;
}
.banner_img:hover img{
    transform: scale(1.2);
    transition: 1s;
}
.hover_text {
    position: absolute;
    top: 120px;
    color: #fff;
    z-index: 20;
    font-weight: 600;
    font-size: 20px;
    width: 230px;
    text-align: center;
}